}

.lds-heart div {
  width: 12px;
  height: 12px;
  background: #ff6b08;
  animation: 1.2s cubic-bezier(.215, .61, .355, 1) infinite lds-heart;
  position: absolute;
  top: 32px;
  left: 32px;
}

.lds-heart div:after, .lds-heart div:before {
  content: " ";
  width: 12px;
  height: 12px;
  background: #ff6b08;
  display: block;
  position: absolute;
}

.lds-heart div:before {
  border-radius: 50% 0 0 50%;
  left: -8px;
}

.lds-heart div:after {
  border-radius: 50% 50% 0 0;
  top: -8px;
}

